import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import AppBar from '../../component/Appbar';
import AppBarMobile from '../../component/Appbar-mobile';
import Content from '../../component/home-content';
import ContentMobile from '../../component/home-content-mobile';
import Headline from '../../component/headline';
import { Box } from '@material-ui/core';
import homeContentMobile from '../../component/home-content-mobile';

const Component = props => {
    const [windowWidth, setWindowWidth] = useState(1024)
    const handleWindowResize = () => {
        setWindowWidth(typeof window !== "undefined" ? window.innerWidth : 0)
    }
    const { classes } = props;
    useEffect(()=> {
        window.addEventListener("resize", handleWindowResize)
        setWindowWidth(typeof window !== "undefined" ? window.innerWidth : 0)
    },[windowWidth])
    console.log(windowWidth)
    return windowWidth > 768 ? (<Box style={{ minHeight: "100vh", backgroundColor: "#E5E5E5", boxSizing: "border-box", padding: "104px 8% 0", display: "flex", flexDirection: "column" }}>
        <AppBar />
        {/* <Headline /> */}
        <Content />
    </Box>
    ) : (
        <Box style={{ minHeight: "100vh", boxSizing: "border-box", background: "#049DD9" }}>
            <AppBarMobile />
            <ContentMobile />
        </Box>
    )
}

Component.propTypes = {

}

export default Component
