import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import {
    Box,
    Container,
    Button
} from '@material-ui/core';
import LogoWhite from '../../asset/logo/logo-fawaid-ilmu-white.png';
import { MenuRounded } from '@material-ui/icons'

const Component = props => {
    const [scrollPosition, setScrollPosition] = useState(0)
    const { classes } = props;
    const menu = ["Artikel Islam", "Info Kajian", "Tentang Kami"];
    const handleWindowScroll = () => {
        setScrollPosition(typeof window !== "undefined" ? window.scrollY : 0)
    }
    useEffect(() => {
        window.addEventListener("scroll", handleWindowScroll);
    }, [])
    return (
        <React.Fragment>
            <Box className={classes.rootWrapper}
            style={{
                position: "fixed",
                width: "100%",
                top: scrollPosition < 50 ? "-100%" : 0,
                left: 0,
                padding: "10px 8%",
                display: "flex",
                justifyContent: "space-between",
                boxSizing: "border-box",
                alignItems: "center",
                zIndex: 100,
                maxHeight: 91,
                background: "#049DD9",
                transition: "top 0.25s ease-in-out",
            }}
            >
                <img src={LogoWhite} style={{ width: 60, height: 20 }} />
                <Button
                style={{
                    color: "#fff",
                    marginRight: -24
                }}
                >
                    <MenuRounded />
                </Button>
            </Box>
            <Box className={classes.rootWrapper}
                style={{
                    position: "block",
                    width: "100%",
                    padding: "10px 8%",
                    display: "flex",
                    justifyContent: "space-between",
                    boxSizing: "border-box",
                    alignItems: "center",
                    zIndex: 100,
                    maxHeight: 91,
                    background: "#049DD9",
                }}
            >
                <img src={LogoWhite} style={{ width: 60, height: 20 }} />
                <Button
                    style={{
                        color: "#fff",
                        marginRight: -24
                    }}
                >
                    <MenuRounded />
                </Button>
            </Box>
        </React.Fragment>
    )
}

Component.propTypes = {

}

export default Component
